.resume-container {
    background-color: white;
    width: 275mm; // A4 width
    min-height: 297mm; // A4 height

    display: grid;
    grid-template-columns: 30% 70%;
    margin: auto;
    box-sizing: border-box;

    padding: 1cm;
    box-sizing: border-box;

    box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);

    // on small screens, stack the columns

    @include screen-sm {
        width: 100%;
        grid-template-columns: 1fr;
        padding: 0.5cm;
        margin: 1rem auto;
    }

    @media print {
        grid-template-columns: 30% 70%;
        padding: 1cm;
        margin: auto;
    }
    




    .resume-left-column {

        .education-item,
        .certification-item {
            margin-bottom: 1rem;

            h3 {
                color: #444;
                margin-bottom: 0.25rem;
            }

            p {
                margin: 0.25rem 0;
                color: #666;
            }
        }
    }

    .resume-right-column {
        padding-left: 2rem;

        @include screen-sm {
            padding-left: 0;
            margin-top: 1rem;
        }

        @media print {
            padding-left: 2rem;
            margin-top: 0;
        }
    }

}

.resume {

    & p,
    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    li {
        font-size: 1rem;
        font-weight: 300;
        @extend .libre-franklin-regular;
        color: black;
    }

    & h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: rgb(0, 34, 85);
    }


    & img {
        height: 2.5rem;
        width: 2.5rem;
    }


    svg {
        fill: black !important;
    }

    & section {

        & ul {
            display: flex;
            flex-direction: column;
            gap: 0.3rem;
        }

        & h2 {
            font-weight: 600 !important;
        }

        & li {
            list-style: none;
        }

    }

    p,
    li {
        font-size: .9rem !important;
        font-weight: 200 !important;
    }

    h2 {
        font-size: 1.5rem !important;
    }



    &-work-experience {
        position: relative;

        & h3 {
            font-weight: 500;
            padding: 0;
            margin: 0;
        }

        & ul {
            margin-top: 1rem;
            margin-left: 1.2rem;
            gap: 0rem !important;
        }

        & li {
            list-style: disc !important;
        }

        &-timeline-dot {
            width: 10px;
            height: 10px;
            background-color: #000000;
            border-radius: 50%;
            position: absolute;
            left: -6px;
            top: 10px;
        }

        &-company {
            display: flex;
            gap: 1rem;
            align-items: center;

        }
    }

    &-education {
        & img {
            display: none;
        }
    }

    &-profile {
        & p {
            position: relative;
        }
    }

    &-section {
        //margin-bottom: 2rem;

        &-header {
            @extend .lora-regular;
            display: flex;
            align-items: center;
            gap: 1rem;
            margin-bottom: 0.5rem;
            font-weight: bold !important;
            //text-decoration: underline;
            //color: rgb(0, 56, 109) !important;

            & h2 {
                font-size: 1.6rem;
                margin: 0;
            }

            & svg {
                width: 1.5rem;
                height: 1.5rem;
                //margin-right: 1rem;
                display: none;
            }
        }

        &-divider {
            border-bottom-color: rgba(0, 0, 0, 0.1);
            border-bottom-width: 1px;
            border-bottom-style: solid;
            margin: 1rem 0;
        }

    }

    &-contact {
        display: flex;
        gap: 1rem;
        align-items: center;

        & svg {
            width: 1rem;
            height: 1rem;
            fill: $color-font-primary;
        }
    }

    &-info {
        display: flex;
        flex-direction: column;
        gap: .2rem !important;
    }

    &-name {
        & h1 {
            font-size: 2.3rem !important;
            font-weight: bold !important;
            //color: rgb(0, 69, 134) !important;
        }

        & p {
            font-size: 1.5rem !important;
            margin-top: -1rem;
        }

        margin-bottom: 1rem;
    }
}

.resume-skills-groups {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;

    .resume-skills-group {
        display: flex;
        gap: 0.5rem;

        h3 {
            //font-size: 0.9rem;
            color: $color-font-primary;
            font-weight: bold;
        }

        ul {
            display: flex;
            flex-wrap: wrap;
            gap: 0.5rem;
        }
    }
}

.resume-project {
    .resume-project-context {
        & span {
            font-style: italic !important;
            font-weight: 300 !important;
            background-color: aqua;
        }
    }
}


@media print {
    @page {
        size: A4;
        margin: 0;
    }

    html,
    body {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;
    }

    .resume {
        width: 210mm;
        min-height: 297mm;    
    }

    .resume-screen-info {
        display: none;
    }

    h3 {
        font-weight: 500 !important;
    }
}


